import {useState, useEffect} from 'react';
import Item from './Item';

const Items = () => {
    const [items, setItems] = useState();
    const [name, setName] = useState();
    const [description, setDescription] = useState();
    const [link, setLink] = useState();
    const [price, setPrice] = useState();

    const getData = () => {
        fetch('https://wishlist-40fdd-default-rtdb.firebaseio.com/wishlist.json')
            .then(res => res.json())
            .then(res => setItems(res))
    }

    const data = {
        "name": name,
        "description": description,
        "link": link,
        "price": price
      };

    const addData = (e) => {
        e.preventDefault();
        setName('');
        setDescription('');
        setLink('');
        setPrice('');

        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(data)
        };
        fetch('https://wishlist-40fdd-default-rtdb.firebaseio.com/wishlist.json', requestOptions)
            .then(response => response.json())
            .then(setTimeout(function() {
                getData();
            }, 500))
    }

    useEffect(() => {
        getData();
    },[]);
    

    return(
        <div>
            {(items && Object.keys(items).length !== 0) ? Object.keys(items).map((key, index) => {return <Item key={key} name={items[key].name} description={items[key].description} link={items[key].link} price={items[key].price}/>}) : "No Data"}

            <form>
                <label>
                    Name:
                    <input type="text" name="name" value={name} onChange={(e) => setName(e.target.value)}/>
                </label>
                <label>
                    Description:
                    <input type="text" name="description" value={description} onChange={(e) => setDescription(e.target.value)}/>
                </label>
                <label>
                    Link:
                    <input type="text" name="link" value={link} onChange={(e) => setLink(e.target.value)}/>
                </label>
                <label>
                    Price:
                    <input type="text" name="price" value={price} onChange={(e) => setPrice(e.target.value)}/>
                </label>
                <input type="submit" value="Submit" onClick={addData}/>
            </form>

        </div>
    );
}

export default Items;